                  <span class="form-group-inputbox-error" v-if="phoneValError">{{phoneValErrorMsg}}</span>
                </div>
                <div class="form-group-inputbox">
                  <h2 class="form-group-inputbox-title">类别</h2>
                  <!-- header-right-time-credit -->
                  <el-select class="header-right-time-credit" clearable placeholder="请选择" v-model="newaccount.typeVal" @change="changeType">
                    <el-option v-for="item in typelist" :value="item.key" :label="item.value" :key="item.key">
                    </el-option>
                  </el-select>
                </div>
              </div>
            </div>
          </div>
