<script>
import {
  bigeventCompanyList,
  surveyCredit
} from '../../config/api'
import {
  dialogConfirm,
  compareJson,
  dialogConfirmHasbtn
} from '../../config/mUtils'
// import payMoneyPage from '../jdzsPage/payMoneyPage';
export default {
  data() {
    return {
      //选择公司下拉框
      companyVal: '',
      companyId: '',
      totalCompanyList: [],
      companyList: [],
      loading: false,
      //账户列表
      accountList: [],
      //弹窗列表-----------------------------数据
      typelist: [{
        key: 2,
        value: '股东'
      }, {
        key: 1,
        value: '董监高'
      }],
      addaccountDialogVisible: false, //新增账户弹窗显示
      addaccountDialogVisible1: false, //付费弹窗
      confirmData: {
        // general:{},
        list: [],
      },
      canSave: false,
      addType: 0, //弹窗类型0=>新建，1=>修改
      accountIndex: 0, //修改时的index
      //新建账户数据
      newaccount: {
        name: '',
        identityId: '',
        phone: '',
        type: '',
        typeVal: ''
      },
      identityIdValErrorMsg: '',
      nameValErrorMsg: '',
      phoneValErrorMsg: '',
      notConfim: true,

      //新的付费弹窗
      // isShow:true,
      // alertData:{
      //     title:'征信已达上限，请购买征信套餐',
      //     type:3,//1、账号付费 2、智能文书3、征信 4、舆情,
      //     fileInfo:[],
      //     // onPrice:0.99,
      //     shopNumber:1,
      // },
    }
  },
  created() {
    //获取公司列表
    this.getCompanyList()
    //获取账户列表
  },
  // components:{payMoneyPage},
  computed: {
    //弹窗--------------------------------------------------------验证
    addaccountDialogTitle: function() {
      if (this.addType == 0) {
        return '添加征信人员'
      } else {
        return '编辑征信人员'
      }
    },
    //身份证验证
    identityIdValError: function() {
      if (this.notConfim) {
        return false
      }
      if (this.newaccount.identityId == '') {
        this.identityIdValErrorMsg = '身份证不能为空'
        return true
      } else {
        let na = this.gettrim(this.newaccount.identityId);
        let reg = /(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        if (!reg.test(na)) {
          this.identityIdValErrorMsg = '身份证格式不正确'
        }
        return !reg.test(na);
      }
    },
    //姓名验证（只能为中文）
    nameValError: function() {
      if (this.notConfim) {
        return false
      }
      if (this.newaccount.name == '') {
        this.nameValErrorMsg = '姓名不能为空'
        return true
      } else {
        return false
      }
    },
    //手机号验证
    phoneValError: function() {
      if (this.notConfim) {
        return false
      }
      // if (this.newaccount.phone == '') {
      //   this.phoneValErrorMsg='手机号不能为空'
      //   return true
      // } else {
      if (this.newaccount.phone != '') {
        let na = this.gettrim(this.newaccount.phone);
        let reg = /^1\d{10}$/;
        if (!reg.test(na)) {
          this.phoneValErrorMsg = '手机号格式不正确'
        }
        return !reg.test(na);
      }
    },
    //submitOk
    submitOk: function() {
      if (!this.identityIdValError && !this.nameValError && !this.phoneValError) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    //格式化身份证
    setIndentity() {
      let na = this.gettrim(this.newaccount.identityId);
      // console.log(na.length);
      if (na.length == 18) {
        this.newaccount.identityId = this.addStr(na)
      }
    },
    //身份证插入空格
    addStr(str) {
      let tmp = str.substring(0, 6);
      let tmp1 = str.substring(6, 14);
      let tmp2 = str.substring(14, 17);
      let tmp3 = str.substring(17, 18);
      str = tmp + ' ' + tmp1 + ' ' + tmp2 + ' ' + tmp3
      return str;
    },
    //格式化手机号
    setPhone() {
      let na = this.gettrim(this.newaccount.phone);
      if (na.length == 11) {
        this.newaccount.phone = this.addStr1(na)
      }
    },
    //手机号插入空格
    addStr1(str) {
      let tmp = str.substring(0, 3);
      let tmp1 = str.substring(3, 7);
      let tmp2 = str.substring(7, 11);
      str = tmp + ' ' + tmp1 + ' ' + tmp2
      return str;
    },
    //去除空格
    gettrim(str) {
      return str.replace(/\s/g, "");
    },
    //删除征信人按钮
    deleteAccountClick(index) {
      dialogConfirmHasbtn('确定删除该征信人？').then(() => {
        // console.log(index);
        // console.log(this.accountList)
        dialogConfirm('success', '删除成功')
        this.accountList.splice(index, 1);
      }).catch(() => {});
    },
    //添加新征信人按钮
    addAccount(id) {
      this.addaccountDialogVisible = true;
      this.accountIndex = id;
      if (id != -1) { //编辑
        this.addType = 1;
        let na = this.accountList[id];
        this.newaccount = {
          name: na.name,
          identityId: na.identityId,
          phone: na.phone,
          type: na.type,
          typeVal: na.typeVal
        }
      } else { //新增
        this.newaccount = {
            name: '',
            identityId: '',
            phone: '',
            type: '',
            typeVal: ''
          },
          this.addType = 0;
      }
    },

    dataChange(str) {
      this.canSave = true
      // if(str!='')
      // str.replace(/\s/g, "");
    },
    changeType() {
      this.newaccount.type = this.newaccount.typeVal;
      for (let item of this.typelist) {
        if (item.key == this.newaccount.typeVal) {
          this.newaccount.typeVal = item.value;
        }
      }
      this.canSave = true

    },
    //弹窗确定按钮
    addAccountClick() {
      this.notConfim = false;
      if (this.submitOk) {
        //编辑
        if (this.addType == 1) {
          let na = this.newaccount;
          this.accountList[this.accountIndex] = {
            name: na.name,
            identityId: na.identityId,
            phone: na.phone,
            type: na.type,
            typeVal: na.typeVal
          }
        } else { //新增
          this.accountList.push(this.newaccount);
        }
        this.addaccountDialogVisible = false;
        this.canSave = false;
        this.notConfim = true;
      }
    },

    //选择公司
    getCompanyList() {
      //获取公司列表
      bigeventCompanyList().then(res => {
        if (res.data.msgNo == 1) {
          this.totalCompanyList = res.data.payload;
          this.companyList = this.totalCompanyList.slice(0, 20);
        }
      })
    },
    changeCompany() {
      this.companyId = this.companyVal;
    },
    //公司搜索
    remoteMethod(query) {
      if (query !== '') {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.companyList = this.totalCompanyList.filter(item => {
            return item.name.indexOf(query) > -1;
          });
        }, 200);
      } else {
        this.companyList = [];
      }
    },

    //添加征询
    toSurveyCredit() {
      // console.log(this.companyVal);
      // console.log(this.accountList);
      if (this.accountList.length <= 0) {
        dialogConfirm('info', '请先添加征信人');
        return false;
      }
      let ae = [];
      let number = 0;
      for (let item of this.accountList) {
        // console.log(item.type);
        let newObj = {
          idCard: item.identityId.split(' ').join(''),
          name: item.name,
          phone: item.phone.split(' ').join(''),
        };
        if (item.type == '') {
          item.type = 0
        }
        newObj.type = item.type;
        ae.push(newObj);

      }
      let params = {};
      if (this.companyVal != '' && this.companyVal != undefined) {
        params.companyId = this.companyVal;
        for (let item of this.totalCompanyList) {
          // console.log(item);
          if (item.id == this.companyVal) {
            params.companyName = item.name
            break;
          }
        }

      }
      params.ae = ae;
      // console.log(params);
      surveyCredit(params).then(res => {
        // console.log(res);
        if (res.data.msgNo == 1) {
          // this.totalCompanyList = res.data.payload;
          // this.companyList = this.totalCompanyList.slice(0, 20);
          dialogConfirm('success', '添加成功')
          this.$router.push({
            name: 'creditQuery'
          })
        } else if (res.data.msgNo == 9528) {
          this.addaccountDialogVisible1 = true;
          this.confirmData = res.data.payload[0]

          // this.isShow = true;
        }
      })
    },
    //取消提交
    toCancleConfirm() {
      // console.log(1111);
      if (this.canSave) {
        dialogConfirmHasbtn('确定不保存退出么？').then(() => {
          this.canSave = false;
          this.addaccountDialogVisible = false;
          this.notConfim = true;
        }).catch(() => {});
      } else {
        this.addaccountDialogVisible = false;
        this.notConfim = true;
      }
    },


    toCancleConfirm1() {
      this.addaccountDialogVisible1 = false;
    },
    // //隐藏
    // hideAlertBox(){
    //   this.isShow = false;
    // },
  }
};

</script>
